 column !important;\r\n  }\r\n  \r\n  .responsive-split > .gutter {\r\n    width: 100% !important;\r\n    height: 8px !important;\r\n    cursor: row-resize !important;\r\n  }\r\n  \r\n  .responsive-split-vertical {\r\n    flex-direction: column !important;\r\n  }\r\n  \r\n  .responsive-split-vertical > .gutter {\r\n    width: 100% !important;\r\n    height: 8px !important;\r\n  }\r\n}\r\n\r\n/* Ensure the mobile sidebar is scrollable */\r\n.sidebar-mobile {\r\n  overflow-y: auto !important;\r\n  max-height: 100vh;\r\n}\r\n\r\n/* Accessibility styles */\r\n.sr-only {\r\n  position: absolute;\r\n  width: 1px;\r\n  height: 1px;\r\n  padding: 0;\r\n  margin: -1px;\r\n  overflow: hidden;\r\n  clip: rect(0, 0, 0, 0);\r\n  white-space: nowrap;\r\n  border-width: 0;\r\n}\r\n\r\n/* High contrast focus styles */\r\n:focus-visible {\r\n  outline: 2px solid #0ea5e9;\r\n  outline-offset: 2px;\r\n}\r\n\r\n/* Improves readability in high contrast mode */\r\n@media (forced-colors: active) {\r\n  * {\r\n    border-color: currentColor;\r\n  }\r\n  \r\n  button, \r\n  a, \r\n  .gutter {\r\n    forced-color-adjust: none;\r\n  }\r\n  \r\n  /* Ensure focus outlines are visible in high contrast mode */\r\n  :focus {\r\n    outline: 2px solid transparent;\r\n    outline-offset: 2px;\r\n  }\r\n}\r\n\r\n/* Reduce motion for users who prefer reduced motion */\r\n@media (prefers-reduced-motion: reduce) {\r\n  *, *::before, *::after {\r\n    animation-duration: 0.01ms !important;\r\n    animation-iteration-count: 1 !important;\r\n    transition-duration: 0.01ms !important;\r\n    scroll-behavior: auto !important;\r\n  }\r\n}\r\n\r\n/* Improved keyboard navigation */\r\n[tabindex]:not([tabindex=\"-1\"]):focus,\r\na:focus,\r\nbutton:focus,\r\ninput:focus,\r\nselect:focus,\r\ntextarea:focus {\r\n  outline: 2px solid #0ea5e9;\r\n  outline-offset: 2px;\r\n}\r\n\r\n/* Skip to content link when focused */\r\n.skip-to-content {\r\n  background: #0ea5e9;\r\n  color: white;\r\n  font-weight: bold;\r\n  left: 50%;\r\n  padding: 8px;\r\n  position: absolute;\r\n  transform: translateY(-100%);\r\n  transition: transform 0.3s;\r\n  z-index: 100;\r\n}\r\n\r\n.skip-to-content:focus {\r\n  transform: translateY(0%);\r\n}\r\n\r\n/* Focus trap for modals - prevent background interaction when modal is open */\r\nbody.modal-open {\r\n  overflow: hidden;\r\n} "],"sourceRoot":""}]);
