.gutter {
  background-color: #eee;
  background-repeat: no-repeat;
  background-position: 50%;
}

.dark .gutter {
  background-color: #2a2a2a;
}

.gutter:hover {
  background-color: #ddd;
}

.dark .gutter:hover {
  background-color: #3a3a3a;
}

.gutter.gutter-horizontal {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  cursor: col-resize;
}

.gutter.gutter-vertical {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  cursor: row-resize;
}

/* Make sure the split container takes full height */
.split {
  display: flex;
  flex-direction: row;
  height: 100%;
  min-height: 100%;
}

.split.vertical {
  flex-direction: column;
}

/* Fix for split panes in flex containers */
.split, .gutter.gutter-horizontal {
  float: left;
}

/* Ensure child containers fill the available height */
.split > div {
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

/* Ensure the preview container fills its parent height */
.preview-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

/* Force editor and preview to use full available height */
.editor, .markdown-preview {
  flex: 1 1 auto;
  min-height: 0;
  height: 100%;
} `, "",{"version":3,"sources":["webpack://./src/splitPane.css"],"names":[],"mappings":"AAAA,uBAAuB;AACvB;EACE,sBAAsB;EACtB,4BAA4B;EAC5B,wBAAwB;AAC1B;;AAEA;EACE,yBAAyB;AAC3B;;AAEA;EACE,sBAAsB;AACxB;;AAEA;EACE,yBAAyB;AAC3B;;AAEA;EACE,yDAA2K;EAC3K,kBAAkB;AACpB;;AAEA;EACE,yDAA2L;EAC3L,kBAAkB;AACpB;;AAEA,oDAAoD;AACpD;EACE,aAAa;EACb,mBAAmB;EACnB,YAAY;EACZ,gBAAgB;AAClB;;AAEA;EACE,sBAAsB;AACxB;;AAEA,2CAA2C;AAC3C;EACE,WAAW;AACb;;AAEA,sDAAsD;AACtD;EACE,gBAAgB;EAChB,aAAa;EACb,sBAAsB;AACxB;;AAEA,yDAAyD;AACzD;EACE,OAAO;EACP,aAAa;EACb,sBAAsB;EACtB,gBAAgB;AAClB;;AAEA,0DAA0D;AAC1D;EACE,cAAc;EACd,aAAa;EACb,YAAY;AACd","sourcesContent":["/* Split pane styling */\r\n.gutter {\r\n  background-color: #eee;\r\n  background-repeat: no-repeat;\r\n  background-position: 50%;\r\n}\r\n\r\n.dark .gutter {\r\n  background-color: #2a2a2a;\r\n}\r\n\r\n.gutter:hover {\r\n  background-color: #ddd;\r\n}\r\n\r\n.dark .gutter:hover {\r\n  background-color: #3a3a3a;\r\n}\r\n\r\n.gutter.gutter-horizontal {\r\n  background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAUAAAAeCAYAAADkftS9AAAAIklEQVQoU2M4c+bMfxAGAgYYmwGrIIiDjrELjpo5aiZeMwF+yNnOs5KSvgAAAABJRU5ErkJggg==');\r\n  cursor: col-resize;\r\n}\r\n\r\n.gutter.gutter-vertical {\r\n  background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAB4AAAAFAQMAAABo7865AAAABlBMVEVHcEzMzMzyAv2sAAAAAXRSTlMAQObYZgAAABBJREFUeF5jOAMEEAIEEFwAn3kMwcB6I2AAAAAASUVORK5CYII=');\r\n  cursor: row-resize;\r\n}\r\n\r\n/* Make sure the split container takes full height */\r\n.split {\r\n  display: flex;\r\n  flex-direction: row;\r\n  height: 100%;\r\n  min-height: 100%;\r\n}\r\n\r\n.split.vertical {\r\n  flex-direction: column;\r\n}\r\n\r\n/* Fix for split panes in flex containers */\r\n.split, .gutter.gutter-horizontal {\r\n  float: left;\r\n}\r\n\r\n/* Ensure child containers fill the available height */\r\n.split > div {\r\n  min-height: 100%;\r\n  display: flex;\r\n  flex-direction: column;\r\n}\r\n\r\n/* Ensure the preview container fills its paren